  findRowsPerExtSys(extSysName: string) {
    this.dummyPendingForAuthDataDetails = new Array<PendingForAuthDetailsDTO>();

    for (const entry of this.pendingForAuthDetailsDTOList) {
      if (entry.sourceCode === extSysName) {
        // console.log(entry); // 1, "string", false
        this.dummyPendingForAuthDataDetails.push(entry);
      }
    }
    this.dataSource = new MatTableDataSource<PendingForAuthDetailsDTO>(
      this.dummyPendingForAuthDataDetails
    );
  }

