  downloadData() {
    this.button1 = false;
    console.log(this.pendingForAuthDetailsDTOList);
    this.pendingForAuthDetailsDTOList.forEach((element) => {
      if (element.drCr == 'C') {
        this.credit = element.lcyEquivalent;
        this.debit = 0;
      } else {
        this.debit = element.lcyEquivalent;
        this.credit = 0;
      }
      this.dataForProcessScreen1.push({
        Batch_No: element.id.batchNo, //1
        //Refrence No : element.id.refNo, // 2 skiiping this user can see this in FlexCube
        Account_Branch: element.accountBranch, //3
        Account: element.account, //4
        Acc_Desc: element.accDesc, //5
        DrCr: element.drCr, //6
        Trn_Code: element.txnCode, //7
        Transaction_Desc : element.trnDesc, //8
        FcyAmount: element.amount, //9
        Exch_Rate: element.exchRate, //10
        DrLcyAmt : this.debit, //11
        CrLcyAmt : this.credit, //12
        Instr_code : element.instrumentNo, //13
        Value_Date: this.pipe.transform(element.valueDate, 'dd-MMM-yy'), //14
        User_Id: element.inputBy, //15
        Authorizer_ID : element.firstTimeAuthorizer, //16
      });  
      console.log(this.dataForProcessScreen1);
    });
    console.log('final data', this.dataForProcessScreen1);
    this.excelService.exportAsExcelFile(this.dataForProcessScreen1, 'Excel_Upload_Data'); // this.pendingForAuthDetailsDTOList[0]
	 this.dataForProcessScreen1=[];
  }

//pdf download
  convert() {
    var doc = new jsPDF({
      orientation: "landscape",
      
    });
    doc.setFont("helvetica");
    doc.setFontType("bold");
    doc.setFontSize(9);
    var col = [["BatchNo",  // 1
            //Refrence No : // 2 skiiping this, user can see this in FlexCube
            "AccBranch", // 3
            "Acc",  // 4
            "AccDesc", // 5
            "DrCr", // 6
            "TrnCode", // 7,
            "TrnDesc",  // 8
            "FcyAmt", // 9
            "Rate", // 10
            "DrLcyAmt", // 11
            "CrLcyAmt", // 12
            "InstrCode", // 13
            "ValueDate", // 14
            "UserId" , // 15
            "AuthId"]]; // 16

    var rows = [];

    /* The following array of object as response from the API req */
    console.log(this.pendingForAuthDetailsDTOList);
    var itemNew = this.pendingForAuthDetailsDTOList;

    itemNew.forEach(element => {
      /** For finding out is lcyAmount 
       * is debited or credited based on that will
       * assign value to credit/debit field
      */
      if (element.drCr == 'C') {
        this.pdfCreditVar = element.lcyEquivalent;
        this.pdfDebitVar = 0;
      } else {
        this.pdfDebitVar = element.lcyEquivalent;
        this.pdfCreditVar = 0;
      }
      this.valueDate = this.pipe.transform(element.valueDate, 'dd-MMM-yy'); //14
      // this.valueDate = new Date(element.valueDate);
      var date = new Date(element.timeForExport).toLocaleDateString("en-us");
      console.log(date)
      var temp = [element.id.batchNo, // 1
                  // Refrence No : element.id.refNo, // 2 skiiping this user can see this in FlexCube
                  element.accountBranch, // 3
                  element.account,  // 4
                  element.accDesc, //5
                  element.drCr, //6
                  element.txnCode, //7
                  element.trnDesc, //8
                  element.amount, //9
                  element.exchRate, //10
                  this.pdfDebitVar, //11
                  this.pdfCreditVar, //12
                  element.instrumentNo, //13
                  this.valueDate, //14
                  element.inputBy, //15
                  element.firstTimeAuthorizer]; //16
      rows.push(temp);
    });
    //doc.autoTable(col, rows);
    doc.autoTable({
      head: col,
      body: rows,
      theme: 'striped',
      margin: {top: 25, bottom: 15},
      styles: {overflow: 'linebreak',
              fontSize: 8},
      showHeader: 'everyPage',
    });
    doc.save('ExcelDAtaAuthFirst.pdf');
  }
